html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.listMain {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0; }
  .listMain::after {
    clear: both;
    content: "";
    display: table; }
  .listMain li {
    display: table;
    width: 100%;
    table-layout: fixed; }
    .listMain li > div {
      vertical-align: middle;
      display: table-cell;
      width: 50%; }
  .listMain .mainDescription {
    padding-left: 1em;
    padding-right: 1em; }

.listSecondary {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin: 2em auto; }
  .listSecondary::after {
    clear: both;
    content: "";
    display: table; }
  @media screen and (max-width: 499px) {
    .listSecondary li {
      float: left;
      display: block;
      margin-right: 10.1484%;
      width: 26.56773%; }
      .listSecondary li:last-child {
        margin-right: 0; }
      .listSecondary li:nth-child(3n) {
        margin-right: 0; }
      .listSecondary li:nth-child(3n+1) {
        clear: left; } }
  @media screen and (min-width: 500px) and (max-width: 794px) {
    .listSecondary li {
      float: left;
      display: block;
      margin-right: 5.85151%;
      width: 15.3188%; }
      .listSecondary li:last-child {
        margin-right: 0; }
      .listSecondary li:nth-child(5n) {
        margin-right: 0; }
      .listSecondary li:nth-child(5n+1) {
        clear: left; } }
  @media screen and (min-width: 795px) {
    .listSecondary li {
      float: left;
      display: block;
      margin-right: 4.11092%;
      width: 10.76207%; }
      .listSecondary li:last-child {
        margin-right: 0; }
      .listSecondary li:nth-child(7n) {
        margin-right: 0; }
      .listSecondary li:nth-child(7n+1) {
        clear: left; } }

.mainTradeImg, .secondaryTradeImg {
  width: 100%; }
  .mainTradeImg a, .secondaryTradeImg a {
    display: block;
    height: 100%; }
  .mainTradeImg iron-image, .secondaryTradeImg iron-image {
    display: block;
    height: 100%;
    width: 100%; }

.mainTradeImg {
  height: 7em; }

.secondaryTradeImg {
  height: 5em; }

.stackHeader {
  margin: 0;
  font-family: "ff-enzo-web", "Raleway", sans-serif;
  color: #40526B;
  margin-bottom: 5px;
  border-bottom: 1px solid #abb9cc; }

.stackDescription {
  display: inline-block;
  margin: 0.25em 0 1.5em 0;
  font-family: "expo-sans-pro", "Droid Serif", serif; }

.overlay {
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  transition: 0.5s;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  height: 100%;
  width: 0;
  background-color: rgba(93, 119, 155, 0.5); }

.overlay-text {
  color: white;
  text-align: center;
  font-weight: bold;
  whitespace: nowrap; }



