html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.horizontal {
  width: 100%; }
  .horizontal ul {
    width: 100%;
    display: table;
    width: 100%;
    table-layout: fixed;
    list-style-type: none;
    padding: 0;
    margin-top: 0.5em; }
  .horizontal li {
    display: table-cell;
    width: 20%;
    border: 1px solid rgba(64, 82, 107, 0.2);
    border-radius: 0;
    -webkit-transition-duration: 0.25s;
    -moz-transition-duration: 0.25s;
    transition-duration: 0.25s;
    -webkit-transition-property: border;
    -moz-transition-property: border;
    transition-property: border;
    border-right: none;
    text-align: center;
    display: inline-block;
    border-collapse: separate; }
    .horizontal li:last-child {
      border-right: 1px solid rgba(64, 82, 107, 0.2); }
    .horizontal li:hover {
      background-color: #d1d9e3;
      color: black;
      border-bottom: 1px solid rgba(64, 82, 107, 0.7); }
    .horizontal li.active {
      color: black;
      background-color: #d1d9e3;
      border-bottom: 1px solid rgba(64, 82, 107, 0.7); }
      .horizontal li.active > a {
        cursor: default; }
  .horizontal a {
    display: block;
    padding: 5px;
    color: #272727;
    font-family: "ff-enzo-web", "Raleway", sans-serif;
    font-size: 110%;
    text-decoration: none; }



