html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.form-field {
  display: table;
  width: 100%;
  table-layout: fixed;
  margin-top: 0.5em;
  padding: 0 1em; }
  .form-field label {
    display: table-cell;
    width: 25%;
    vertical-align: middle;
    text-align: center; }

.field {
  display: table-cell;
  width: 75%; }
  .field input, .field select, .field textarea {
    width: 100%;
    padding: 3px; }

.button-field {
  display: table;
  width: 100%;
  table-layout: fixed;
  text-align: right; }
  .button-field button, .button-field input[type="submit"], .button-field input[type="reset"] {
    display: table-cell;
    width: 20%; }



