html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.container {
  padding-top: 1em;
  padding-bottom: 1em;
  max-width: 80%;
  margin-left: auto;
  margin-right: auto; }
  .container::after {
    clear: both;
    content: "";
    display: table; }

.row {
  display: table;
  width: 100%;
  table-layout: fixed;
  border: 1px solid rgba(64, 82, 107, 0.2);
  border-radius: 0; }
  .row:hover {
    border-color: rgba(64, 82, 107, 0.7); }
  .row > div {
    vertical-align: middle;
    display: table-cell;
    width: 50%; }

.img {
  text-align: center; }
  .img img {
    width: 50%;
    display: inline-block; }



