html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.location {
  width: 80%;
  margin: 2em auto; }

.location-row {
  display: table;
  width: 100%;
  table-layout: fixed;
  padding: 0.25em 0; }
  .location-row span {
    display: table-cell;
    width: 50%;
    text-align: center;
    font-family: "ff-enzo-web", "Raleway", sans-serif; }

.location-info {
  text-transform: uppercase; }

.contact-form {
  border: 1px solid rgba(64, 82, 107, 0.2);
  border-radius: 0;
  width: 70%;
  margin: 0 auto 2em auto;
  color: #10151b;
  border-top: none;
  border-bottom: none; }
  .contact-form input, .contact-form select, .contact-form textarea, .contact-form button {
    border: 1px solid rgba(64, 82, 107, 0.2);
    border-radius: 0;
    padding: 0.5em; }
    .contact-form input:hover, .contact-form select:hover, .contact-form textarea:hover, .contact-form button:hover {
      border-color: rgba(64, 82, 107, 0.7); }

.g-recaptcha {
  margin: 1em auto;
  width: 10em;
  display: flex;
  content-align: center;
  display: block;
  text-align: center; }

.form-header {
  text-align: center; }

.form-field label {
  font-family: "ff-enzo-web", "Raleway", sans-serif;
  padding-right: 2em;
  text-align: right; }



