html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.app {
  font-family: "ff-enzo-web", "Raleway", sans-serif;
  background-color: #40526B;
  text-align: center; }
  .app h2 {
    display: inline; }
  .app img {
    width: 2em;
    display: inline; }
  .app a {
    text-decoration: none;
    color: white; }
  .app h1 {
    font-size: 250%;
    margin: 0;
    padding: 0.25em;
    font-weight: lighter; }
  .app nav, .app div {
    width: 60%;
    margin: 0 auto; }



