html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

.container {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  padding: 1em 0; }
  .container::after {
    clear: both;
    content: "";
    display: table; }
  .container > div {
    display: table;
    width: 100%;
    table-layout: fixed;
    border: 1px solid rgba(64, 82, 107, 0.2);
    border-radius: 0; }
    .container > div:hover {
      border-color: rgba(64, 82, 107, 0.7); }



